addDays(date) {
  let dt = new Date(date);
  dt.setDate(dt.getDate() + 1);
  return this.preZero(dt.getDate()) + "-" + this.preZero(dt.getMonth() + 1) + "-" + dt.getFullYear();;
}
preZero(val): string {
  // Prepend zero if smaller than 10
  return val < '10' ? '0' + val : val;
}

