___CSS_LOADER_EXPORT___.push([module.id, `ion-tab-button {
  --color: gray; /* Default color */
  --color-selected: var(--primary); /* Active color */
  transition: color 0.3s ease-in-out;
}

ion-tab-button.active-tab {
  color: var(--primary);
  fill: var(--primary);
}

ion-tab-button[aria-selected=true] {
  color: var(--primary); /* Active text color */
}

ion-icon[class*=fn-] {
  background-size: contain;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  width: 1em;
  height: 1em;
}
ion-icon[class*=fn-navigate] {
  background-image: url('icons8-home.svg');
  -webkit-mask-image: url('icons8-home.svg');
  mask-image: url('icons8-home.svg');
  color: red;
}
ion-icon svg #path {
  color: red;
  fill: red !important;
}

#path {
  color: red !important;
  fill: red !important;
}

.label-color {
  color: var(--iconsAndButtonsColor);
}

.tab-icon {
  fill: var(--primary);
}

.tab-selected .themeone-label-color {
  color: #191966;
  font-family: Roboto !important;
}

.themeone-label-color {
  font-family: "Roboto", sans-serif !important;
}

.themeone-tabs {
  font-family: "Roboto", sans-serif !important;
}

.seabirdTheme ion-tab-button {
  color: #000;
  font-weight: bold;
}
.seabirdTheme ion-tab-button.tab-selected {
  color: #D42027;
  font-weight: bold;
}
.seabirdTheme ion-tab-button.tab-selected img {
  filter: invert(19%) sepia(90%) saturate(7000%) hue-rotate(360deg) brightness(90%) contrast(103%);
}

.textBold {
  font-weight: 700;
  font-size: 12px;
