import { Injectable } from '@angular/core';

@Injectable()
export class AppData {
  APP_ID: string;
  IOS_ID: string;
  APP_NAME: string;
  ONE_SIGNAL_APP_ID: string;
  SENDER_ID: string;
  BASE_URL: string;
  isIndonesia: Boolean;
  isAfrica: boolean;
  isIndia: boolean;
  mobileWebVersion: string;
  iosVersion: string;
  isIOS: boolean;
  isANDROID: boolean;
  isWEBAPP: boolean;
  newTheme: any;

  constructor() {
    this.APP_ID = "com.app.tsrtc";
    this.IOS_ID = "com.app.tsrtc";
    this.APP_NAME = "TSRTC";
    this.ONE_SIGNAL_APP_ID = "";
    this.SENDER_ID = "";

    //At a time one country needs to be selected
    this.isIndonesia = false;
    this.isAfrica = false;
    this.isIndia = true;

    //At a time one platform needs to be selected
    this.isIOS = true;
    this.isANDROID = false;
    this.isWEBAPP = true;

    //Mobile web or IOS version can change from here
    this.mobileWebVersion = "40";
    this.iosVersion = "4.3";



        // this.BASE_URL = location.origin + '/';
        this.BASE_URL = "https://www.tsrtconline.site/";
          // this.BASE_URL = "https://www.tgsrtcbus.in/";
        // this.BASE_URL = "https://tgsrtc.tgsrtcbus.com/"
        



  }

  baseUrl() {
    return this.BASE_URL;
  }
}